import React from "react";
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Blog from '../components/Blog';
import { globalHistory } from "@reach/router";
import Metadata from '../components/Metadata';
import {BlogPageMetadata} from '../configs/PageMetadata';

const useStyles = makeStyles(() => ({
  msgCard: {
    margin: 20,
    padding: 20
  }
}));

export default function BlogPage() {
  const classes = useStyles();
  var blogId = undefined;
  if (globalHistory.location.hash !== undefined) {
    blogId = globalHistory.location.hash.replace("#", '');
    console.log(globalHistory.location);
  }
  else {
    blogId = false;
  }

  const onPreRenderHTML = ({
    getHeadComponents,
    replaceHeadComponents,
  }) => {
    const headComponents = getHeadComponents()
    const order = ["title", "base", "meta", "link", "noscript", "script", "style"]
  
    const sortedHeadComponents = headComponents
      .slice(0)
      .flat()
      .sort((x, y) => {
        return order.indexOf(x.type) - order.indexOf(y.type)
      })
  
    replaceHeadComponents(sortedHeadComponents)
  }

  return (
    <Layout>
      <Metadata
        Title={BlogPageMetadata.Title}
        Description={BlogPageMetadata.Description}
      />
      { (blogId === undefined || blogId === "") ?
        (<Paper elevation={3} className={classes.msgCard} >
          <Typography variant="h5">
            Blog Not Found !
          </Typography>
        </Paper>)
        : (<Blog blogId={blogId} />)
      }
      {
        //onPreRenderHTML()
      }
    </Layout>
  );
}